<template>
    <div>
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
            <van-list>
        <van-cell v-for="(item,index) in blog" :key="index">
            <template #title>
                <span class="custom-title">{{item.emp_name}}</span>
            </template>
            <template #label>
                <span class="custom-title">No.{{index+1}}</span>
            </template>
            <template #default>
                <span class="custom-title"
                      style="background-color: #ddd;
                      text-align:center;
                      color: orangered;display:inline-block;
                      width: 40px;height: 20px;border-radius: 5px">{{item.blog_count}}</span>
            </template>
        </van-cell>
            </van-list>
        </van-pull-refresh>
        <van-cell></van-cell>
        <van-cell></van-cell>
        <van-cell></van-cell>
    </div>
</template>

<script>
    import { Toast } from 'vant'
    export default {
        name: "blog_amount",
        data(){
          return{
              blog:[],
              isLoading: false,
          }
        },
        mounted() {
            this.$axios.get('https://zhuzuoji.com/myapi/rank/getRankByBlogCount',{
                params:{key:'vv9544'}
            }).then(res=>{
                if (res.status==200&&res.data.state){
                    this.blog=res.data.rank_data
                }else {
                    this.$toast.fail('查询失败')
                }
            }).catch(err=>{
                let error=err.toString()
                if(error.indexOf('Network')!=-1){
                    this.$toast.fail('请检查网络')
                }
            })
        },
        methods:{
            onRefresh() {
                this.$axios.get('https://zhuzuoji.com/myapi/rank/getRankByBlogCount',{
                    params:{key:'vv9544'}
                }).then(res=>{
                    if (res.status==200&&res.data.state){
                        this.blog=res.data.rank_data
                    }
                })
                setTimeout(() => {
                    Toast('刷新成功');
                    this.isLoading = false;
                }, 1000);
            },
        }
    }
</script>

