<template>
    <div>
        <van-cell v-for="(item,index) in amount" :key="index">
            <template #title>
                <span class="custom-title">{{item.emp_name}}</span>
            </template>
            <template #label>
                <span class="custom-title">No.{{index+1}}</span>
            </template>
            <template #default>
                <span class="custom-title"
                      style="background-color: #ddd;
                      text-align:center;
                      color: orangered;display:inline-block;
                      width: 40px;height: 20px;border-radius: 5px">{{item.moments_count}}</span>
            </template>
        </van-cell>
        <van-cell></van-cell>
        <van-cell></van-cell>
        <van-cell></van-cell>
    </div>
</template>

<script>
    export default {
        name: "blog_amount",
        data(){
            return{
                amount:[]
            }
        },
        mounted() {
            this.$axios.get('https://zhuzuoji.com/myapi/rank/getRankByMomentsCount',{
                params:{key:'vv9544'}
            }).then(res=>{
                if (res.status==200&&res.data.state){
                    this.amount=res.data.rank_data
                }
            }).catch(err=>{
                let error=err.toString()
                if(error.indexOf('Network')!=-1){
                    this.$toast.fail('请检查网络')
                }
            })
        }
    }
</script>

