<template>
    <div>
        <van-cell v-for="(item,index) in rank" :key="index">
            <template #title>
                <span class="custom-title">{{item.emp_name}}</span>
            </template>
            <template #label>
                <span class="custom-title">No.{{index+1}}</span>
            </template>
            <template #default>
                <span class="custom-title"
                      style="background-color: #ddd;text-align:center;color: orangered;display:inline-block;width: 40px;height: 20px;border-radius: 5px">{{item.emp_inte}}</span>
            </template>
        </van-cell>
        <van-cell></van-cell>
        <van-cell></van-cell>
        <van-cell></van-cell>
    </div>
</template>

<script>
    export default {
        name: "points_rank",
        data(){
            return{
                rank:[]
            }
        },
        mounted() {
            this.$axios.get('https://zhuzuoji.com/myapi/rank/getRankByInte',{
                params:{key:'vv9544'}
            }).then(res=>{
                if(res.status==200&&res.data.state){
                    this.rank=res.data.rank_data
                }else {
                    this.$toast.fail('查询失败')
                }
            }).catch(err=>{
                let error=err.toString()
                if(error.indexOf('Network')!=-1){
                    this.$toast.fail('请检查网络')
                }
            })
        }
    }
</script>

<style scoped>
    .custom-title {
        margin-right: 4px;
        vertical-align: middle;
    }
    .custom-titl .inte{
        background-color: #ddd;
    }
</style>